import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';


@Component({
  selector: 'app-menu-visor-estadal',
  templateUrl: './menu-visor-estadal.component.html',
  styleUrls: ['./menu-visor-estadal.component.css']
})
export class MenuVisorEstadalComponent implements OnInit {

  VacunasMenu: boolean = false;
  VacunasPerdidas: boolean = false;

  usuarios: any =[];

  constructor(private auth: UserService) { }

  ngOnInit(): void {
    this.informacionUsuario();
  }

  informacionUsuario() {
    this.auth.profile().subscribe(
    res => {
    this.usuarios = res;
    // alert(this.usuarios.user.centro_salud_id);
    console.log('usuario data', res);
    
    },
    err => {
    alert('Sesion Experiada..');

    }
    );
    }

  logout() {
    this.auth.logout();
  }

}
